import "./index.scss";

import React from "react";

import { BoldLacaText, RegularText } from "../../atoms";
import { Cards } from "../../organisms";

const CardBlock = () => {
  return (
    <div className="cards--block">
      <div className="cards-title">
        <BoldLacaText text={"Dlaczego my?"} />
      </div>
      <div className="cards-subtitle">
        <RegularText
          text={
            "Gdy masz problem, musisz działać szybko. Nie ma czasu by szukać, dzwonić i pytać. "
          }
        />
      </div>
      <Cards />
    </div>
  );
};

export default CardBlock;
