import "./index.scss";

import React from "react";

import { BoldLacaText } from "../../atoms";
import { Opinion } from "../../organisms";

const OpinionBlock = () => {
  return (
    <div className="opinions--wrapper">
      <div className="opinions-title">
        <BoldLacaText text={"Opinie"} />
      </div>
      <div className="opinions">
        <Opinion
          name={"Paweł"}
          text={
            "Wybrany przez unilaw prawnik pomóg mił i nakierował mnie na odpowiednie rozwiązanie. Napisał oświadczenie i pomógł w spisaniu zasad."
          }
        />
        <Opinion
          name={"Kasia"}
          text={
            "Profesjonalne podejście do klienta. Sprawa, którą miałam wydawała się beznadziejna. Dzięki wskazanemu freelancerowi sprawa skończyła się pozytywnie. Polecam serdecznie."
          }
        />
        <Opinion
          name={"Kasia"}
          text={
            "Profesjonalne podejście do klienta. Sprawa, którą miałam wydawała się beznadziejna. Dzięki wskazanemu freelancerowi sprawa skończyła się pozytywnie. Polecam serdecznie."
          }
        />
      </div>
    </div>
  );
};

export default OpinionBlock;
