import "./index.scss";

import React from "react";

import { Progress } from "../../organisms";

const ProgressBlock = ({ progress, text, additionalClassname }) => {
  return (
    <div className="progress--wrapper">
      <Progress
        progress={progress}
        text={text}
        additionalClassname={additionalClassname}
      />
    </div>
  );
};

export default ProgressBlock;
