import "./index.scss";

import React from "react";

import { CounselorImage, Counselors, CounselorText } from "../../organisms";

const CounselorBlock = () => {
  return (
    <div className="counselor--block">
      <div>
        <CounselorText />
        <Counselors />
      </div>
      <div className={"counselor--image-wrapper"}>
        <CounselorImage />
      </div>
    </div>
  );
};

export default CounselorBlock;
