import "./index.scss";

import React, { useEffect, useState } from "react";

import Slider from "react-slick";

import { useWindowDimensions } from "../../../helpers/hooks";
import { BoldLacaText, Partner } from "../../atoms";

const PartnersBlock = () => {
  const { width } = useWindowDimensions();
  const [slidesToShow, setSlidesToShow] = useState();

  const settings = {
    // dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: slidesToShow,
    slidesToScroll: 1,
    autoplay: true,
    centerMode: true,
    arrows: false
  };

  function getSlidesToShow() {
    if (466 > width) {
      return 2;
    } else if (604 > width) {
      return 3;
    } else if (768 > width) {
      return 4;
    } else if (768 > width) {
      return 4;
    } else if (958 > width) {
      return 5;
    } else if (1248 > width) {
      return 6;
    } else {
      return 7;
    }
  }

  useEffect(() => {
    setSlidesToShow(getSlidesToShow());
  }, [width]);

  return (
    <div className="partners--wrapper">
      <div className="partners-title">
        <BoldLacaText text={"Pracujemy z najepszymi"} />
      </div>
      <Slider {...settings}>
        <Partner
          link={"https://www.amazon.com/"}
          image={require("../../../assets/raster/amazon.png").default}
        />
        <Partner
          link={"https://jquery.com/"}
          image={require("../../../assets/raster/jquery.png").default}
        />
        <Partner
          link={"https://www.hubspot.com/"}
          image={require("../../../assets/raster/hubspot.png").default}
        />
        <Partner
          link={""}
          image={require("../../../assets/raster/areem.png").default}
        />
        <Partner
          link={"https://bitcoin.org/"}
          image={require("../../../assets/raster/bitcoin.png").default}
        />
        <Partner
          link={"https://www.canon.pl/"}
          image={require("../../../assets/raster/canon.png").default}
        />
        <Partner
          link={"https://www.philips.pl/"}
          image={require("../../../assets/raster/philips.png").default}
        />
      </Slider>
    </div>
  );
};

export default PartnersBlock;
