import "./index.scss";

import React from "react";

import { BoldLacaText } from "../../atoms";
import { CardInfo } from "../../molecules";

const CardInfoBlock = () => {
  return (
    <div className="card-info--wrapper">
      <div className="card-info-title">
        <BoldLacaText text={"Bądź tam, gdzie Twoi klienci"} />
      </div>
      <div className="card-info-cards">
        <CardInfo
          link={require("../../../assets/vector/steps.svg").default}
          text={
            "W 2019 roku aż 61% konsumentów doceniało wygodę i łatwość zakupu usługi. W 2020 roku wartość ta wzrosła jeszcze bardziej - o 41%."
          }
        />
        <CardInfo
          link={require("../../../assets/vector/browser.svg").default}
          text={
            "Większość osób szuka specjalistów w sieci. Jeśli nie będziesz dostępny online, pójdą do Twojej konkurencji."
          }
        />
        <CardInfo
          link={require("../../../assets/vector/laptop.svg").default}
          text={
            "Rezerwacja konsultacji jeszcze nigdy nie była tak prosta! Wykorzystaj tę szansę i zacznij zarabiać więcej z Unilaw."
          }
        />
      </div>
    </div>
  );
};

export default CardInfoBlock;
